.header__logo {
  background-image: url(../../../images/logoMobile.svg);
  background-position: center;
  background-repeat: no-repeat;
  object-fit: contain;
  width: 103.74px;
  height: 24.4px;
  margin-left: 27px;
}

@media screen and (min-width: 768px) {
  .header__logo {
    background-image: url(../../../images/logo.svg);
    width: 142px;
    height: 33px;
    margin-left: 35px;
  }
}

@media screen and (min-width: 1280px) {
  .header__logo {
    margin-left: 0px;
  }
}
